@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400;600;800&display=swap);
/*Barlow font*/

* {
    margin: 0;
    padding: 0;
    font-family: "Barlow", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background: #EFEFEF;
    background: var(--branco);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

:root {
    --azul: #191E74;
    --amarelo:#FEC309;
    --preto: #222222;
    --branco: #EFEFEF;
    --borda: solid 3px;
}

.container-lg {
    width: 92.5%;
    max-width: 1000px;
    height: auto;
    margin: auto;
}

.container-wd {
    width: 89%;
    max-width: 850px;
    height: auto;
    margin: auto;
}

.logo {
    width: 120px;
}

.titulo {
    font-weight: 800;
    font-size: 2.5em;
    text-align: center;
}

.texto-bold {
    font-weight: 600;
    text-align: center;
}

.texto-light {
    font-weight: 400;
    text-align: center;
}

.texto-branco {
    color: #EFEFEF;
    color: var(--branco);
}

.texto-preto {
    color: #222222;
    color: var(--preto);
}

i {
    font-size: 30px;
}

i.fa-whatsapp {
    color: #4EBF61;
}

i.fa-facebook {
    color: #3F90FB;
}

i.fa-instagram {
    background: -webkit-linear-gradient(#485FAB, #EF2D76, #FFE292);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

i.fa-map-marker-alt {
    color: #D93B2D;
}

select, button {
    background: transparent;
    border: solid 3px #FEC309;
    border: var(--borda) var(--amarelo);
    border-radius: 50px;
    outline: none;
}

select {
    padding: 2.5px 10px;
}

button {
    display: flex;
    padding: 5px 20px;
    box-shadow: inset 0 0 8px #FEC309, 0 0 8px #FEC309;
    box-shadow: inset 0 0 8px var(--amarelo), 0 0 8px var(--amarelo);
}

button i {
    font-size: 1.25em;
    padding-right: 4px;
}
header {
    background: var(--azul);
    border-bottom: var(--borda) var(--amarelo);
}

header div.menu {
    height: auto;
    padding: 7.5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header .menu nav {
    display: none;
    position: absolute;
    top: 64.26px;
    right: 0;
}

.menu-toggle div {
    height: 3px;
    width: 20px;
    background: var(--branco);
    margin: 2px 0;
    border-radius: 25px;
}

header .menu.on nav {
    display: flex;
    flex-direction: column;
    height: auto;
    width: 100vw;
    background: var(--preto);
}

header .menu.on nav li {
    padding: 10px 25px;
    text-align: center
}

header .menu.on .two {
    opacity: 0;
}

header .menu.on .one {
    transform: rotate(45deg) translate(3px, 3px)
}

header .menu.on .three {
    transform: rotate(-45deg) translate(4px, -4px)
}

header .menu .one, .two, .three {
    transition-duration: 0.3s;
}

header nav div {
    border-top: var(--borda) #444;
    width: 65%;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

header .menu.on nav div li {
    padding: 10px;
}

@media screen and (min-width: 804px) {
    .menu-toggle {
        display: none;
    }

    header .menu nav {
        display: block;
        position: unset;
        margin: auto;
    }

    header nav ul {
        display: flex;
    }

    header nav ul li {
        padding: 0 30px;
    }

    header nav div {
        display: none;
    }

    header nav ul li a {
        transition-duration: 0.2s;
    }

    header nav ul li a:hover {
        color: var(--amarelo);
        text-shadow: 0 0 1.5px var(--amarelo);
        transition-duration: 0.2s;
    }
}
.banner {
    height: calc(100vh - 64.26px);
    min-height: 200px;
    width: 100%;
    background: var(--branco);
}

.home {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    min-height: 320px;
}

.home h1 {
    min-height: 200px;
}

.home button {
    cursor: pointer;
}

@media screen and (min-width: 588px) {
    .home .titulo {
        font-size: 3em;
    }
}
#produtos {
    margin: 80px auto;
}

#produtos .filtros {
    margin: 30px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#produtos .filtros li {
    margin: 5px 0;
}

#produtos article, #produtos article > div {
    border: solid 0.5px #999;
    border-radius: 10px;
}

#produtos article {
    max-width: 200px;
    padding: 5px;
    margin: 0 5px;
}

#produtos article div.container-img {
    height: 150px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

#produtos article div.dados-produto {
    padding: 10px;
}

#produtos article div.dados-produto .texto-bold {
    font-size: 16px;
}

#produtos article div.dados-produto .texto-light {
    font-size: 14px;
    text-align: start;
}

#produtos article div.dados-produto .descricao {
    margin: 10px 0;
    text-align: center;
}

#produtos article div.dados-produto .preco {
    text-align: start;
    color: darkgreen;
}

#produtos article button {
    margin-top: 10px;
    border-radius: 10px;
    padding: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
    border: none;
    background: var(--azul);
    color: var(--branco);
    cursor: pointer;
}

/*Botões do carousel*/
#produtos button {
    box-shadow: none;
    padding: 15px;
}
#quemsomos {
    margin: 80px auto;
    height: auto;
}

#quemsomos li {
    display: flex;
    align-items: center;
    padding: 10px 0;
    text-align: left;
}

#quemsomos ul {
    margin: 30px auto;
    width: auto;
    min-width: auto;
    max-width: 270px;
}

.circulo {
    height: 10px;
    width: 10px;
    background-color: var(--azul);
    border-radius: 100%;
    margin-right: 10px;
}
#visitenos p {
    margin: 30px 0;
}

#visitenos .endereco {
    text-align: center;
}

#visitenos iframe {
    width: 100%;
    display: block;
    margin: auto;
    max-width: 380px;
    border: var(--borda) var(--amarelo);
    border-radius: 10px;
}
#contato {
    margin: 80px auto;
}

#contato ul li a {
    display: flex;
    align-items: center;
}

#contato ul li a {
    padding: 10px;
}

#contato ul li i {
    margin-right: 10px;
}

#contato ul {
    display: flex;
    flex-direction: column;
    align-items: center;
}
footer {
    height: auto;
    width: 100%;
    background: var(--azul);
    margin-top: 80px;
    border-top: var(--borda) var(--amarelo);
}

footer .logo {
    margin: 0 auto;
    display: block;
    padding: 15px 0;
}

footer p {
    padding-bottom: 15px;
}
